import * as React from 'react';
import Section from '../Section';
import BackgroundSlider from '../BackgroundSlider';
import { Image } from '@chakra-ui/core';
import { SliderItem } from '../BackgroundSlider/BackgroundSlider';

export default function Partners({ images = [], ...props }) {
  const [images1, images2] = React.useMemo(
    () =>
      images.reduce(
        (acc, curr, index) => {
          if (index % 2 === 0) acc[0].push(curr);
          else acc[1].push(curr);
          return acc;
        },
        [[], []]
      ),
    [images]
  );
  return (
    <Section {...props}>
      <BackgroundSlider numberOfElements={images1.length} duration={35}>
        {images1.map((img) => (
          <SliderItem key={img}>
            <Image src={`/images/partners/${img}`} alt={img.replace('png', '')} />
          </SliderItem>
        ))}
      </BackgroundSlider>
      <BackgroundSlider numberOfElements={images2.length} duration={40}>
        {images2.map((img) => (
          <SliderItem key={img}>
            <Image src={`/images/partners/${img}`} alt={img.replace('png', '')} />
          </SliderItem>
        ))}
      </BackgroundSlider>
    </Section>
  );
}
